import React, { useState, useEffect, useRef } from 'react';
import { Typography, Button, Form, Modal, message, Checkbox } from 'antd';
import { CheckCircleFilled, CloseCircleFilled } from '@ant-design/icons';
import logo from './logo.svg';
import './App.css';
import Learner from './components/Learner';
import Scorm from './scorm/Scorm';
import CompleteButton from './components/CompleteButton';
import Mcq from './components/Mcq';


const ResolveQuestionnaireModal = ({ visible, onCancel, onSubmit, question, form, handleAnswerChange }) => (
  <Modal title="Resolver Cuestionario" visible={visible} onCancel={onCancel} footer={null}>
    <Form
      form={form}
      name="resolveQuestionnaire"
      layout="vertical"
      onFinish={onSubmit}
    >
      {question ? (
        <div key={question._id} style={{ marginBottom: 16 }}>
          <Typography.Text>{question.questionText}</Typography.Text>
          {question.pista && (
            <Typography.Text style={{ display: 'block', margin: '8px 0', color: 'grey' }}>
              Pista: {question.pista}
            </Typography.Text>
          )}
          <Form.Item
            name={`question_${question._id}`}
            rules={[{ required: true, message: 'Por favor seleccione una respuesta' }]}
          >
            <Checkbox.Group onChange={checkedValues => handleAnswerChange(question._id, checkedValues)}>
              {question.options.map((option, optionIndex) => (
                <Checkbox key={`${question._id}-${optionIndex}`} value={option.text}>
                  {option.text}
                </Checkbox>
              ))}
            </Checkbox.Group>
          </Form.Item>
        </div>
      ) : (
        <Typography.Text>No hay preguntas disponibles para este tiempo.</Typography.Text>
      )}
      <Form.Item>
        <Button type="primary" htmlType="submit" style={{ width: '100%' }}>
          Enviar Respuestas
        </Button>
      </Form.Item>
    </Form>
  </Modal>
);

const App = () => {
  const [learnerName, setLearnerName] = useState('');
  const [assessment, setAssessment] = useState([]);
  const videoRef = useRef(null);
  const [questions, setQuestions] = useState([]);
  // Estado del modal y la pregunta actual
  const [isResolveModalVisible, setIsResolveModalVisible] = useState(false);
  const [currentQuestion, setCurrentQuestion] = useState(null);
  const [form] = Form.useForm();
  const [selectedAnswers, setSelectedAnswers] = useState({});
  
  useEffect(() => {
    Scorm.init();
    setLearnerName(Scorm.getLearnerName());
    fetch(`${process.env.PUBLIC_URL}/question.json`)
      .then(response => response.json())
      .then(data => {
        setQuestions(data);
        console.log(data)
      })
      .catch(error => console.error('Error fetching questions:', error));
  }, []);

  useEffect(() => {
    console.log('Current questions state:', questions);
  }, [questions]);

  const finish = () => {
    Scorm.finish();
  };

  // Funcin que pausa el video cuando se alcanza el tiempo de pausa
  const handleTimeUpdate = () => {
    const videoElement = videoRef.current;
    if (videoElement) {
      questions.forEach((question, index) => {
        const questionTime = parseFloat(question.time);
        if (videoElement.currentTime >= questionTime && videoElement.currentTime < questionTime + 0.3) {
          videoElement.pause();
          setCurrentQuestion(question);
          setIsResolveModalVisible(true);
        }
      });
    } else {
      console.log('Video element is not defined in handleTimeUpdate');
    }
  };

  // useEffect para aadir y remover el event listener del video
  useEffect(() => {
    const videoElement = videoRef.current;
    console.log(questions)
    if (videoElement) {
      videoElement.addEventListener('timeupdate', handleTimeUpdate);
    }
    return () => {
      if (videoElement) {
        videoElement.removeEventListener('timeupdate', handleTimeUpdate);
      }
    };
  }, [questions]);

  // Funcin para manejar el cambio de respuesta seleccionada
  const handleAnswerChange = (questionId, checkedValues) => {
    setSelectedAnswers(prev => ({ ...prev, [questionId]: checkedValues }));
  };  

  // Funcin para manejar el envo del formulario de resolver cuestionario
  const handleResolveFormSubmit = async () => {
    const question = currentQuestion;
    if (question) {
      const selectedAnswer = selectedAnswers[question._id] || [];
  
      let resultado = "incorrect";
      if (question.options && question.options.length > 0) {
        const correctAnswers = question.options.filter(option => option.isCorrect).map(option => option.text);
        const isCorrect = correctAnswers.length === selectedAnswer.length && correctAnswers.every(answer => selectedAnswer.includes(answer));
        resultado = isCorrect ? "correct" : "incorrect";
        message.open({
          content: (
            <span>
              {isCorrect ? <CheckCircleFilled style={{ color: 'green', marginRight: '8px' }} /> : <CloseCircleFilled style={{ color: 'red', marginRight: '8px' }} />}
              {isCorrect ? "Correcto" : "Incorrecto"}
            </span>
          ),
          duration: 2,
        });
  
        console.log(isCorrect);
        console.log(selectedAnswer.join(', '));

        Scorm.submitMCQ(isCorrect, selectedAnswer.join(', '));
  
        if (isCorrect) {
          setAssessment([...assessment, true]);
          if (assessment.length + 1 === questions.length) {
            finish();
          }
        } else {
          setAssessment([...assessment, false]);
        }

        // Calcular puntaje y estado de xito
        const correctAnswersCount = assessment.filter(result => result === true).length + (isCorrect ? 1 : 0);
        const totalQuestions = questions.length;
        const score = (correctAnswersCount / totalQuestions) * 100;
        const successStatus = correctAnswersCount === totalQuestions ? 'passed' : 'failed';

        Scorm.setScore(score);
        Scorm.setSuccessStatus(successStatus);
        
      }
    }
    setIsResolveModalVisible(false);
    setCurrentQuestion(null);
    form.resetFields();
  };     

  // Funcin para cerrar el modal de resolver cuestionario y resetear el formulario
  const handleResolveCancel = () => {
    setIsResolveModalVisible(false);
    setCurrentQuestion(null);
    form.resetFields();
  };  
  
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <Learner name={learnerName} />
      </header>
      <main>
        <video ref={videoRef} style={{ width: '100%', height: 'auto', paddingTop: '20px' }} controls>
          <source src={`${process.env.PUBLIC_URL}/video.mp4`} type="video/mp4" />
        </video>
        <CompleteButton completeActivity={finish} />
      </main>
      <ResolveQuestionnaireModal
        visible={isResolveModalVisible}
        onCancel={handleResolveCancel}
        onSubmit={handleResolveFormSubmit}
        question={currentQuestion}
        form={form}
        handleAnswerChange={handleAnswerChange}
      />
    </div>
  );
}

export default App;
