import { SCORM } from 'pipwerks-scorm-api-wrapper';

let Scorm = {
  init() {
    SCORM.init();
  },

  getLearnerName() {
    return SCORM.get('cmi.core.student_name');
  },

  submitMCQ(correct, response) {
    let nextIndex = SCORM.get("cmi.interactions._count", true);
    console.log(`Submitting MCQ: index = ${nextIndex}, response = ${response}, correct = ${correct}`);
  
    SCORM.set("cmi.interactions." + nextIndex + ".id", "question_" + nextIndex);
    SCORM.set("cmi.interactions." + nextIndex + ".type", "choice");
    
    // Asegrate de que la respuesta del estudiante sea una cadena
    SCORM.set("cmi.interactions." + nextIndex + ".student_response", String(response));
    
    // Asegrate de que el resultado sea un valor de tipo cadena apropiado
    SCORM.set("cmi.interactions." + nextIndex + ".result", correct ? "correct" : "incorrect");
  
    console.log('MCQ data submitted');
  },

  setScore(score) {
    console.log(`Setting score: ${score}`);
    SCORM.set('cmi.core.score.raw', score);
    SCORM.save();
  },

  setSuccessStatus(status) {
    console.log(`Setting success status: ${status}`);
    SCORM.set('cmi.core.lesson_status', status);
    SCORM.save();
  },
  
  finish() {
    console.log('you have finished!');
    SCORM.set('cmi.core.lesson_status', 'completed');
    SCORM.save();
  }
}

export default Scorm;
